import React, { useState } from 'react';
import { v4 } from 'uuid';
import axios from 'axios';
import { Modal  } from '../components/Modal';
import { Layout  } from '../components/Layout';
import { Card  } from '../components/Card';
import { list, errorMess, newPost } from './styles/PostStyles';

const BASE_URL ='https://simple-blog-api.crew.red/posts';

export default function New() {
    const[post, setPost] = useState({ id: 0, title: '', body: ''});
    const[error, setError] = useState('');
    const[isModalOpen, setIsModalOpen] = useState(false);

    const handleSubmitCreate = async (e) => {
        e.preventDefault();

        if(!post.title || !post.body) {
            setError('Why do you want to send an empty post? Write something!');
            return;
        }

        await axios({
            method: 'post',
            url: `${BASE_URL}`,
            data: post,
        });


        setIsModalOpen(true);
        setPost({ id: 0, title: '', body: ''});
    };

    const handleTitle = (event) => {
        const id = v4().substring(0, 6);
        setError('');
        setPost({
            ...post,
            id: id,
            title: event.target.value,
        });
    };

    const handleBody = (event) => {
        setError('');
        setPost({
            ...post,
            body: event.target.value,
        });
    };

    const closeModal = () => {
        setIsModalOpen(false);
    }

    return (
        <Layout>
            <Card title="Create new post now!">
              <form action="#" onSubmit={handleSubmitCreate}>
                  {error && <errorMess.TitleError>{error}</errorMess.TitleError>}
                <newPost.Label htmlFor="title">
                  <p>Post`s title</p>
                  <newPost.Input
                    placeholder="Input Title of Post"
                    id="title"
                    type="text"
                    value={post.title}
                    onChange={handleTitle}
                  />
                </newPost.Label>
                <newPost.Label htmlFor="body">
                  <p>Post`s text</p>
                  <newPost.Textarea
                    placeholder="Input Body of Post"
                    id="body"
                    value={post.body}
                    onChange={handleBody}
                  />
                </newPost.Label>
                <newPost.Button type="submit">Create post</newPost.Button>
              </form>
            </Card>
            {isModalOpen && <Modal onClose={closeModal}/>}
        </Layout>
    );
}